.PageRoot {
  display: flex;
  flex-direction: row;
  min-height: 100vh;
  font-family: "poppins";
}

.pageRootBlack {
  padding: 50px;
  padding-left: 5%;
  text-align: left;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
  color: var(--white);
  font-weight: lighter;
  background-color: var(--black);
}

.leftPart {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 40vh;
  min-width: 30vw;
  background-color: var(--offwhite);
}

.leftPart img {
  width: 12vw;
  padding: 4px;
  border-radius: 50%;
  border: 0px solid var(--white);
  box-shadow: 7px 7px 14px #c7c7c7, 7px 7px 14px #f3f3f3;
}

.leftPartText {
  margin: 20px;
  font: var(--black) "Myraid Pro";
  font-size: 1.6em;
  font-weight: bolder;
}

@media only screen and (max-width: 768px) {
  .PageRoot {
    flex-direction: column;
    /* width: 40%; */
  }
  .pageRootBlack {
    padding: 50px;
    /* padding-left: 20%; */
    align-items: start;
  }
  .leftPart img {
    width: 40vw;
  }
}
