  .PageRoot {
    min-height: 100vh;
    background-color: whitesmoke;
    padding: 50px;
    padding-left: 100px;
  }
  
  .pageRootWhite {  
    padding: 10px 0px;
    text-align: left;
    display: flex;
    justify-content: center;
    flex: 1;
    color: #666;
    font-weight: lighter;
  }
  
  .leftPart {
    width: 37%;
    padding-right: 20px;
    text-align:right;
  }
  
  @media only screen and (max-width: 425px) {
    .PageRoot{
        padding: 20px;
        padding-left: 20%;
    }
    .pageRootWhite {
      align-items: start;
    }
    .leftPart {
        width: 15%;
    }
  }
  